

.question {
    display: flex;
    margin-top: 15px;
    justify-content: space-between;
    align-content: center;
    border-radius: 20px;
    height: 20px;
    cursor: pointer;
    background: linear-gradient(
      100deg,
      rgb(142, 61, 235) 50%,
      rgb(101, 31, 180) 50%
    );
  
    position: relative;
    color: white;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    padding: 20px;
    transition: 0.3s;
    animation: anime-left 0.3s forwards;
  }
  


  .back{
    margin: 0 auto;
    background-color: rebeccapurple;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    cursor: pointer;
    color: rgb(238, 238, 238);
    font-family: number;
    width: 20%;
    height: 30px;
  }