@media (max-width: 800px) {
  .container {
    min-height: 100vh;
  }
  .contentForms {
    display: grid;
    justify-content: center;
  }

  .image {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px;
  }

  .input {
    height: 40px;
    width: 80%;
    text-indent: 10px;
    border-radius: 5px;
    border: 1px solid;
    margin-top: 20px;
    border: 1px solid #eee;
    display: block;
    font-size: 1rem;
    padding: 0.8rem;
    border-radius: 0.4rem;
    background-color: rgb(255, 255, 255);
    transition: 0.1s;
  }

  .input:focus,
  .input:hover {
    outline: none;
    border-color: #56298b;
    background-color: white;
    box-shadow: 0 0 0 3px #ac7fe3ab;
  }

  .content {
    color: rgb(161, 161, 161);
    margin-top: 30px;
  }
  .entrar {
    position: relative;
    top: 100px;
    margin: 0 auto;
    width: 90%;
  }

  .button {
    font-size: 1rem;
    font-family: var(--type-first);
    cursor: pointer;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.4rem;
    margin-top: 40px;
    background-color: #6f55ff;
    color: #fffffff1;
    padding: 0.8rem 3.2rem;
    box-sizing: border-box;
    transition: 0.2s;
  }

  .button:hover,
  .button:focus {
    outline: none;
    box-shadow: 0 0 0 3px #dcc0ffab, 0 0 0 4px #6f55ff;
  }

  .button:disabled {
    opacity: 0.5;
    cursor: wait;
  }

  .link {
    margin-top: 20px;
  }

  .links {
    top: 10px;
    position: relative;
    display: block;
  }

  .error {
    color: rgb(221, 14, 14);
    font-size: 0.87rem;
    margin-left: 10px;
    font-weight: 400;
    margin-top: 0.25rem;
  }

  .errorGeral {
    text-align: center;
    padding: 10px;
    color: #ffffff;
    border-radius: 5px;
    background-color: rgba(241, 60, 60, 0.603);
  }
}
