.container {
  margin: 0 auto;
  background-color: rgb(248, 248, 248);
  min-height: 100vh;
  padding: 10px;
}

h4 {
  text-transform: capitalize;
}

.title {
  font-family: var(--type-first);
  margin: 1rem 0;
  color: #0e001e;
  position: relative;
  font-family: number;
  font-size: 2rem;
  line-height: 1;
}

.title::after {
  height: 15px;
  content: "";
  display: block;
  width: 15px;
  border-radius: 2px;
  position: absolute;
  bottom: 0;
  z-index: -1;
  background-color: rgb(218, 183, 253);
}

.boxContent {
  height: 150px;
  width: 150px;
  border-radius: 5px;
  display: flex;
  background-color: rgb(255, 255, 255);
  align-items: center;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.boxContent img {
  width: 60%;
}

.boxfixed {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.titleElement {
  margin-top: 20px;
}

.text {
  line-height: 1.6rem;
}
