.container {
  min-height: 100vh;
  margin: 0 auto;
  font-family: "number", Cambria, Cochin, Georgia;
  width: 95%;
}

.h2 {
  text-shadow: 1px 1px 2px rgba(0,0,0,1);
color: rgb(81, 194, 81);
letter-spacing: 1px;
text-transform: uppercase;
text-align: center;
}

.title {
  font-family: var(--type-first);
  margin: 1rem 0;
  color: #0e001e;
  position: relative;
  font-weight: 500;
  font-size: 2rem;
  line-height: 1;
}

.title::after {
  height: 15px;
  content: "";
  display: block;

  width: 15px;
  border-radius: 2px;
  position: absolute;
  bottom: 0;
  z-index: -1;
  background-color: #7ddd10;
}

.message {
  font-size: 0.8rem;
  color: rgb(124, 124, 124);
  margin-bottom: 5px;
}

.title::before {
  --slice-0: inset(50% 50% 50% 50%);
  --slice-1: inset(80% -6px 0 0);
  --slice-2: inset(50% -6px 30% 0);
  --slice-3: inset(10% -6px 85% 0);
  --slice-4: inset(40% -6px 43% 0);
  --slice-5: inset(80% -6px 5% 0);
  width: 80px;
  content: "Mrplato";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    45deg,
    transparent 3%,
    #a0f600 3%,
    #a0f600 5%,
    #ffe601 5%
  );
  text-shadow: -3px -3px 0px #f5f4e7, 3px 3px 0px #00e6f6;
  clip-path: var(--slice-0);
  animation: 1s glitch;

  animation-timing-function: steps(2, end);
}

.containerNav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.button {
  background-color: #7ddd10e5;
  height: 40px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  display: flex;
  transition: 0.3s;
  color: rgb(66, 66, 66);
  align-items: center;
  justify-content: center;
  background: linear-gradient(to left top, #7ddd10 50%, #7acf18 50%);
  width: 90px;
  border-radius: 5px;
}

.button.disabled {
  opacity: 50%;
}

.button:hover {
  font-size: 1.1rem;
  color: rgb(241, 232, 222);
}

.containerNav {
  border-bottom: 1px solid rgb(221, 215, 215);
}

.online {
  padding: 10px;
}
.containerUser {
  width: 90%;
  margin: 0 auto;
}
.user {
  display: flex;
  align-items: center;
  margin-top: 10px;
  border-radius: 5px;
  padding: 5px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  background-color: #ffffff;
  justify-content: space-between;
}

.imageUser {
  height: 50px;
  width: 50px;
  background-image: url(../../assets/profile/profile.png);
  background-size: contain;
  border-radius: 5px;
}

.score {
  display: flex;
  border-radius: 10px;
  padding: 8px;
  gap: 10px;
  align-items: center;
}

.button,
.button::after {
  background-color: #7ddd10e5;
  height: 40px;
  font-size: 1.2rem;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  display: flex;
  border-radius: 5px;
  transition: 0.3s;
  color: rgb(66, 66, 66);
  align-items: center;
  justify-content: center;
  background: linear-gradient(to left top, #7ddd10 50%, #7acf18 50%);
  width: 90px;

  border: 0;
  color: rgb(49, 49, 49);
  font-family: number;
  outline: transparent;
  position: relative;
}

.button::after {
  --slice-0: inset(50% 50% 50% 50%);
  --slice-1: inset(80% -6px 0 0);
  --slice-2: inset(50% -6px 30% 0);
  --slice-3: inset(10% -6px 85% 0);
  --slice-4: inset(40% -6px 43% 0);
  --slice-5: inset(80% -6px 5% 0);

  content: "Mrplato";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    45deg,
    transparent 3%,
    #a0f600 3%,
    #a0f600 5%,
    #ffe601 5%
  );
  text-shadow: -3px -3px 0px #f5f4e7, 3px 3px 0px #00e6f6;
  clip-path: var(--slice-0);
  animation: 1s glitch;

  animation-timing-function: steps(2, end);
}

button:hover::after {
  animation: 1s glitch;
  animation-timing-function: steps(2, end);
}

@keyframes glitch {
  0% {
    clip-path: var(--slice-1);
    transform: translate(-20px, -10px);
  }
  10% {
    clip-path: var(--slice-3);
    transform: translate(10px, 10px);
  }
  20% {
    clip-path: var(--slice-1);
    transform: translate(-10px, 10px);
  }
  30% {
    clip-path: var(--slice-3);
    transform: translate(0px, 5px);
  }
  40% {
    clip-path: var(--slice-2);
    transform: translate(-5px, 0px);
  }
  50% {
    clip-path: var(--slice-3);
    transform: translate(5px, 0px);
  }
  60% {
    clip-path: var(--slice-4);
    transform: translate(5px, 10px);
  }
  70% {
    clip-path: var(--slice-2);
    transform: translate(-10px, 10px);
  }
  80% {
    clip-path: var(--slice-5);
    transform: translate(20px, -10px);
  }
  90% {
    clip-path: var(--slice-1);
    transform: translate(-10px, 0px);
  }
  100% {
    clip-path: var(--slice-1);
    transform: translate(0);
  }
}

.bartime {
  height: 5px;
  background-color: #a0f600;
}

.questionbox {
  background-color: white;
  margin-top: 20px;
  padding: 20px;
  font-weight: 600;
  text-align: justify;
  font-family: myFirstFont;
  border-radius: 5px;
}

.answerbox {
  display: flex;
  margin-top: 40px;
  gap: 40px;
  align-items: center;
  justify-content: center;
  min-height: 50px;
}

.buttontrue {
  height: 100%;
  text-align: center;
  color: #333;
  width: 100px;
  padding: 20px;
  border-radius: 5px;
  background-color: #8fc74f;
}

.buttonfalse {
  width: 100px;
  border-radius: 5px;
  color: #333;

  text-align: center;
  padding: 20px;

  background-color: #e76e65;
}

.boxranking {
  text-align: center;
}

.boxranking span {
  font-size: 2rem;
}

.progressbar {
  width: 0%;
  background-color: rebeccapurple;
  height: 5px;
}

.progressbar.activepro {
  animation: progress 11s linear forwards;
}

@keyframes progress {
  0% {
    width: 0%;
    background-color: #00e6f6;
  }
  79% {
    width: 79%;
    background-color: #c5da11;
  }
  80% {
    width: 80%;
    background-color: #e76e65;
  }
  100% {
    width: 100%;
    background-color: rebeccapurple;
  }
}
