.container {
  margin: 0 auto;
  width: 95%;
  min-height: 100vh;
}

h4 {
  text-transform: capitalize;
}

.title {
  font-family: var(--type-first);
  margin: 1rem 0;
  color: #0e001e;
  font-family: number;
  position: relative;
  font-size: 2rem;
  line-height: 1;
}

.title::after {
  height: 15px;
  content: "";
  display: block;
  width: 15px;
  border-radius: 2px;
  position: absolute;
  bottom: 0;
  z-index: -1;
  background-color: rgb(218, 183, 253);
}

.containermessage {
  margin-top: 30px;
}
.photo {
  background-image: url(../../assets/profile/profile.png);
  height: 70px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border-radius: 5px;
  width: 70px;
}

.ask {
  background-color: rgb(172, 137, 206);
  width: 50px;
  height: 20px;
  font-family: number;
  display: flex;
  align-content: center;
  justify-content: center;
  color: white;
  border-radius: 5px;
  padding: 10px;
}
.headerCommunity {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.messagebox {
  height: 80px;
  margin-top: 15px;
  border-radius: 6px;
  display: flex;
  padding: 10px;
  gap: 10px;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  background-color: rgb(255, 255, 255);
}

.messagebox p {
  display: inline-block;
  height: 1.5em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.content {
  position: relative;
  display: flex;
  width: 75%;
  align-items: center;
  height: 100%;
}
.answers {
  position: absolute;
  bottom: 0;
  font-size: 0.8rem;
  color: #686768;
}

.date {
  position: absolute;
  bottom: 0;
  right: 0;
  font-size: 0.7rem;
}
.username {
  position: absolute;
  top: 0;
  text-transform: capitalize;
  font-weight: 800;
}

.containerText {
  margin: 25px 0px;
}

.button {
  border: none;
  display: flex;
  justify-content: center;
  font-family: number;
  align-items: center;
  margin-top: 15px;
  border-radius: 5px;
  height: 50px;
  width: 50%;
  color: white;
  background-color: rgb(172, 137, 206);
}

.input {
  height: 30px;
  width: 80%;
  text-indent: 10px;
  border-radius: 5px;
  border: 1px solid;
  margin-top: 20px;
  margin-bottom: 20px;
  border: 1px solid #eee;
  display: block;
  font-size: 1rem;
  padding: 0.8rem;
  border-radius: 0.4rem;
  background-color: rgb(255, 255, 255);
  transition: 0.1s;
}

.input:focus,
.input:hover {
  outline: none;
  border-color: #56298b;
  background-color: white;
  box-shadow: 0 0 0 3px #ac7fe3ab;
}

.askbox {
  background-color: white;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
  margin-top: 40px;
  position: relative;
}

.headerCommunityElement {
  display: flex;
}

.informationuser {
  width: 80px;
  margin-left: 10px;
  display: grid;
}

.score {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 0.8rem;
  font-weight: 600;
}

.dateCommunity {
  font-size: 0.8rem;
  margin-top: 10px;
  color: #b8b6b9;
}
.usernameCommunity {
  font-size: 1.4rem;
  font-weight: 600;
}

.optionCommunity {
  position: absolute;
  right: 10px;
  top: 5px;
  font-weight: 900;
}

.askbox p {
  margin-top: 25px;
  font-size: 1.2rem;
}

.answerbox {
  border-radius: 5px;
  padding: 10px;
  padding: 8px;
  margin-top: 40px;
  position: relative;
  border-bottom: 1px solid rgb(207, 207, 207);
}
.answerbox p {
  margin-top: 25px;
  font-size: 1.2rem;
}

.answer {
  background-color: rgb(172, 137, 206);
  width: 50px;
  font-family: number;

  height: 20px;
  display: flex;
  align-content: center;
  justify-content: center;
  color: white;
  border-radius: 5px;
  padding: 15px;
}

.answerbutton {
  display: flex;
  margin: 30px;
  align-items: center;
  justify-content: center;
}

.containerTextElement {
  margin-bottom: 50px;
}

.editform {
  margin-top: 20px;
}
