

.containerGroup{
    margin: 0 auto;
    padding: 0;
    min-height: 140vh;
    width: 90%;
    position: relative;
    margin-top: 20px;
}


.user{
    background-image: url(../../assets/default/test2.webp);
    background-size: contain;
    background-position: center;
    height: 30px;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    align-content: center;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    justify-content: center;
    font-family: number;
    margin-top: 20px;
    
}
.user.self{
border:  2px solid rebeccapurple;
}

.title {
    font-family: var(--type-first);
    margin: 1rem 0;
    color: #0e001e;
    position: relative;
    font-weight: 500;
    font-size: 2rem;
    line-height: 1;
    font-family: number;
  }
  
  .title::after {
    height: 15px;
    content: "";
    display: block;
  
    width: 15px;
    border-radius: 2px;
    position: absolute;
    bottom: 0;
    z-index: -1;
    background-color: #ad5eee;
  }
  
  .message {
    font-size: 0.8rem;
    color: rgb(124, 124, 124);
    margin-bottom: 5px;
  }
  
  .title::before {
    --slice-0: inset(50% 50% 50% 50%);
    --slice-1: inset(80% -6px 0 0);
    --slice-2: inset(50% -6px 30% 0);
    --slice-3: inset(10% -6px 85% 0);
    --slice-4: inset(40% -6px 43% 0);
    --slice-5: inset(80% -6px 5% 0);
    width: 80px;
    content: "Mrplato";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
      45deg,
      transparent 3%,
      #a0f600 3%,
      #a0f600 5%,
      #ffe601 5%
    );
    text-shadow: -3px -3px 0px #f5f4e7, 3px 3px 0px #00e6f6;
    clip-path: var(--slice-0);
    animation: 1s glitch;
  
    animation-timing-function: steps(2, end);
  }

  @keyframes glitch {
    0% {
      clip-path: var(--slice-1);
      transform: translate(-20px, -10px);
    }
    10% {
      clip-path: var(--slice-3);
      transform: translate(10px, 10px);
    }
    20% {
      clip-path: var(--slice-1);
      transform: translate(-10px, 10px);
    }
    30% {
      clip-path: var(--slice-3);
      transform: translate(0px, 5px);
    }
    40% {
      clip-path: var(--slice-2);
      transform: translate(-5px, 0px);
    }
    50% {
      clip-path: var(--slice-3);
      transform: translate(5px, 0px);
    }
    60% {
      clip-path: var(--slice-4);
      transform: translate(5px, 10px);
    }
    70% {
      clip-path: var(--slice-2);
      transform: translate(-10px, 10px);
    }
    80% {
      clip-path: var(--slice-5);
      transform: translate(20px, -10px);
    }
    90% {
      clip-path: var(--slice-1);
      transform: translate(-10px, 0px);
    }
    100% {
      clip-path: var(--slice-1);
      transform: translate(0);
    }
  }
  .status{
    display: flex;
    margin: 10px;
    font-family: number;
    justify-content: center;
    align-content: center;
    border-radius: 100%;

}

.exit{
    padding: 10px;
    background-color: rgb(196, 74, 74);
    width: 100px;
    bottom: 0;
    border-radius: 5px;
    position: absolute;
    left:0;
    right:0;
    margin-left:auto;
    margin-right:auto;
    font-family: number;
    text-align: center;
    margin-top: 20px;
    color: rgb(240, 239, 239);
}
.containerExit{
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 20px;
    justify-content: center;
}


.button {
    background-color: #7ddd10e5;
    height: 40px;
    margin:30px 0px;
    font-family: number;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    display: flex;
    transition: 0.3s;
    color: rgb(46, 46, 46);
    align-items: center;
    justify-content: center;
    background: linear-gradient(to left top, #7ddd10 50%, #7acf18 50%);
    width: 90px;
    border-radius: 5px;
  }

  .containerChat{
