@media (max-width: 800px) {
  @font-face {
    font-family: "number";
    src: url(../../fonts/JerseyM54-aLX9.ttf);
  }

  .container {
    margin: 0;
  }

  .containerIntern {
    margin: 5%;
    border-radius: 5px;
  }

  .boxRules {
    padding: 5px;
    height: 500px;
    border-radius: 5px;
  }

  .buttonBox {
    display: flex;
    gap: 10px;
    justify-content: space-around;
  }
  .buttonBox2 {
    display: flex;
    width: 80%;
    gap: 15px;
  }

  .buttonbox3 {
    height: 400px;
    margin-top: 10px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    padding: 5px;
    border-radius: 5px;
    overflow-y: auto;
  }

  .statement {
    font-size: 1.2rem;
    padding: 10px;
    margin-bottom: 20px;
  }
  .buttonBox li {
    background-color: rgb(238, 198, 113);
    cursor: pointer;
    display: flex;
    justify-content: center;
    height: 40px;
    width: 120px;
    align-items: center;
    font-weight: 600;
    color: #333;
    border-radius: 5px;
  }
  .buttonBox2 li {
    background-color: rgb(238, 198, 113);
    padding: 10px;
    cursor: pointer;
    font-weight: 600;
    color: #333;
    border-radius: 5px;
  }

  .button {
    padding: 5px;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 50%;
    border-radius: 5px;
    text-align: center;
    border: none;
    cursor: pointer;
    color: white;
    background-color: rgb(104, 106, 248);
  }
  .buttondeactive {
    padding: 5px;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 50%;
    border-radius: 5px;
    text-align: center;
    color: white;
    background-color: rgb(180, 179, 179);
  }

  .boxConclusion {
    padding: 10px;
    min-height: 550px;
    margin-top: 50px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: 5px;
  }

  .boxbuttoninput {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    margin-top: 50px;
  }

  .buttoninput {
    background-color: rgb(104, 106, 248);
    border-radius: 5px;
    height: 50px;
    color: rgb(248, 243, 234);
    cursor: pointer;
    display: flex;
    font-weight: 600;
    align-items: center;
    justify-content: center;
    width: 50px;
  }

  .buttoninputselect {
    background-color: rgb(104, 106, 248);
    border: none;
    border-radius: 5px;
    height: 50px;
    color: rgb(248, 243, 234);
    cursor: pointer;
    display: flex;
    font-weight: 600;
    align-items: center;
    justify-content: center;
    width: 50px;
  }

  .buttoninputdelete {
    background-color: rgb(209, 88, 88);
    cursor: pointer;
    border-radius: 5px;
    color: rgb(248, 243, 234);
    display: flex;
    font-weight: 600;
    padding: 5px;
    align-items: center;
    justify-content: center;
  }

  .buttoninputconclusion {
    background-color: rgb(44, 165, 70);
    cursor: pointer;
    border-radius: 5px;
    color: rgb(248, 243, 234);
    display: flex;
    font-weight: 600;
    padding: 5px;
    align-items: center;
    justify-content: center;
  }

  .inputText {
    background-color: rgb(235, 235, 235);
    padding: 10px;
    min-height: 20px;
    margin-top: 20px;
    border-radius: 5px;
  }

  .boxselect {
    height: 300px;
    overflow-y: auto;
    margin-top: 20px;
    border-radius: 5px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    padding: 10px;
  }

  .boxbutton {
    background-color: rgb(215, 215, 236);
    height: 100px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    margin-top: 50px;
    justify-content: space-around;
  }

  .boxselect hr {
    border: 1px dashed rgb(129, 129, 129);
  }

  .conclusion {
    background-color: rgba(207, 207, 207, 0.616);
    display: flex;
    border-radius: 5px;
    padding: 5px;
    align-items: center;
  }

  .headerQuestion {
    display: flex;
    justify-content: space-between;
  }

  .back{
    margin: 0 auto;
    background-color: rebeccapurple;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    cursor: pointer;
    color: rgb(238, 238, 238);
    font-family: number;
    width: 20%;
    margin-bottom: 20px;
    height: 30px;
  }
}
