.progressbar {
  width: 0%;
  background-color: rebeccapurple;
  height: 5px;
}

.activeprogress {
  animation: progress 10s linear;
}
.bartime {
  height: 5px;
  background-color: #a0f600;
}

@keyframes progress {
  0% {
    width: 0%;
    background-color: #00e6f6;
  }
  79% {
    width: 79%;
    background-color: #c5da11;
  }
  80% {
    width: 80%;
    background-color: #e76e65;
  }
  100% {
    width: 100%;
    background-color: rebeccapurple;
  }
}
