.container {
  min-height: 100vh;
  margin: 0 auto;
  position: relative;
  width: 95%;
}

.title {
  font-family: var(--type-first);
  margin: 1rem 0;
  color: #0e001e;

  position: relative;
  font-weight: 500;
  font-size: 1.7rem;
  line-height: 1;
}

.title::after {
  height: 15px;
  content: "";
  display: block;
  width: 15px;
  border-radius: 2px;
  position: absolute;
  bottom: 0;
  z-index: -1;
  background-color: #6f55ff;
}

.NavLink {
  display: flex;
  align-items: center;
  padding: 10px;
  gap: 20px;
}

.containerNavLink {
  display: flex;
  gap: 10%;
  align-items: center;
}

.containerScore {
  font-family: number;
  border-radius: 5px;
}

.hexagon h1 {
  color: white;
}

.hexagon {
  height: 300px;
  display: grid;
  font-family: number;
  color: white;
  position: relative;
  margin-top: 50px;
  border-radius: 2px;
  background-size: contain;
  background-image: url(../../assets/default/istockphoto-1369249687-612x612.jpg);
  text-align: center;
  width: 100%;
}
.position {
  position: absolute;
  left: 10px;
  text-align: center;
  top: 10px;
  font-size: 1rem;
  border-radius: 5px;
}
.hexagon:before,
.double:after {
  content: "";
}
.hexagon:before {
  top: 4px; /* border width */
  left: 4px; /* border width */
  height: calc(100% - 8px); /* 100% - (2 * border width) */
  width: calc(100% - 8px); /* 100% - (2 * border width) */
}
.hexagon,
.hexagon:before,
.double:after {
  -webkit-clip-path: polygon(
    50% 0%,
    100% 25%,
    100% 75%,
    50% 100%,
    0% 75%,
    0% 25%
  );
  clip-path: polygon(95% 0%, 100% 5%, 100% 100%, 5% 100%, 0% 95%, 0% 0%);
}

.containerPerformance {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  margin-bottom: 50px;
  gap: 20px;
}

.performance {
  height: 200px;
  border-radius: 5px;
  width: 150px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
/* Just for demo */

.anima {
  transform: translateX(-20px);
  animation: anime-left 0.3s forwards;
}

@keyframes anime-left {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.containerEdit {
  display: grid;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  justify-content: center;
  margin-top: 50px;
  padding: 20px;
  align-items: center;
}

.data p {
  background-color: #d8d8d8;
  margin-top: 10px;
  padding: 2px;
  text-indent: 5px;
  border-radius: 2px;
}

.selectImage {
  background-color: rgb(85, 85, 85);
  height: 500px;
  z-index: 1000;
  border-radius: 5px;
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  width: 350px;
  overflow-y: auto;

  justify-content: space-between;
  position: absolute;
}

.modalselect {
  width: 100%;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  height: 800px;
  border-radius: 5px;
  background-color: rgba(44, 43, 43, 0.315);
  position: absolute;
}

.image {
  height: 80px;
  width: 80px;
  border-radius: 5px;
  background-color: red;
}

.close {
  font-size: 5rem;
  color: white;
  position: absolute;
  top: 10px;
  right: 10px;
}
