@media (max-width: 800px) {
  .activeInterface {
    background-color: rgb(223, 168, 51);
    color: #333;
    transition: 0.1s;
    box-shadow: 0 0 0 3px #fea, 0 0 0 4px #f4b900;
    border-radius: 5px;
  }

  .inputselect {
    min-height: 50px;
    border-radius: 2px;
    font-size: 0.7rem;
    display: flex;
    background-color: white;
    align-items: center;
    justify-content: space-between;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    cursor: pointer;
    margin-top: 15px;

    transition: 0.1s;
  }

  .inputselect.selected {
    background-color: rgb(180, 164, 252);
    color: white;
  }

  .inputselect span {
    height: 50px;
    justify-content: center;
    align-items: center;
    display: flex;
    color: white;
    background-color: rebeccapurple;
    width: 20px;
    border-radius: 2px;
  }
}
