@media (max-width: 800px) {
  .header {
    height: 80px;
    display: flex;
    align-items: center;
    padding: 15px;
    transition: 0.3s;
    gap: 15px;
    background-color: rgb(255, 255, 255);
    position: relative;
    justify-content: space-between;
    background-position: center;
    background-size: contain;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  }

  .header.activemenu {
    height: 140px;
  }

  .photoprofile {
    height: 60px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 5px;
    background-color: rgb(255, 255, 255);

    width: 60px;
  }

  .navlink {
    left: 0;
    right: 0;
    margin: auto;
    align-items: center;
    justify-content: center;
    display: flex;
    margin-bottom: 10px;
    position: absolute;
    width: 70%;
    bottom: 0;
    right: 0;
    justify-content: space-around;
  }

  .navlink li {
    font-weight: 600;
  }

  .containerHeader {
    display: flex;
    gap: 20px;
    align-items: center;
  }

  .miniscore {
    display: flex;
    gap: 5px;
    align-items: center;
    font-size: 0.7rem;
    font-weight: 800;
  }
  .profileHeader h3 {
    margin: 0;
    text-transform: capitalize;
    font-size: 1.3rem;
  }
  .containerMenu {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  .home {
    padding: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
