@import url('https://fonts.googleapis.com/css?family=Lato');


.chat-container {
	background-color: rgba(0, 0, 0, 0.4);
	border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 5px 0px, rgba(0, 0, 0, 0.1) 0px 0px 1px 0px;
    height: 400px;
    
    margin-top: 50px;
	padding: 20px;
	position: relative;
}

.chat {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
    height: 400px;
	list-style-type: none;
    overflow-y: scroll;
	padding: 0;
	margin: 0;
}




.message {
	background-color: rgba(245, 245, 245, 0.9);
	border-radius: 5px;
	position: relative;
    
	margin-bottom: 30px;
}

.message.left {
	padding: 15px;
    position: relative;

}

.message.right {
	align-self: flex-end;
	padding: 15px;
    position: relative;
}



.message.right .logo {
	left: auto;
	right: 10px;
}

.message p {
	margin: 0;
}

.text_input {
	position: absolute;
	bottom: -20px;
    border: none;
    box-sizing: border-box;
	left: 0;
    width: 100%;
	right: 0;
}


.text_input {
    height: 50px;
    text-indent: 10px;
    border-radius: 5px;
    border: 1px solid;
    border: 1px solid #eee;
    display: block;
    font-size: 1rem;
    padding: 1rem;
    border-radius: 0.4rem;
    background-color: rgb(255, 255, 255);
    transition: 0.1s;
  }

  .text_input:focus,
  .text_input:hover {
    outline: none;
    border-color: #56298b;
    background-color: white;
    box-shadow: 0 0 0 3px #ac7fe3ab;
  }



.name {
    color: rgb(255, 255, 255);
    top: -20px;
    left: 0;
    text-transform: capitalize;
    position: absolute;
    font-size: .9rem;
}