

.containerTournamment{
    margin: 0 auto;
    width: 90%;
    min-height: 100vh;
}

.groupContainer{
    background-image: url(../../assets/default/test.jpg);
    display: flex;
    gap: 20px;
    background-position: center;
    background-size: contain;
    margin-top: 20px;
    padding: 20px;
    font-family: number;
    align-items: center;
    justify-content: space-between;
    height: 30px;
    border-radius: 10px;
    color: white;
}
.status{
    display: flex;
    margin: 10px;
    font-family: number;
    justify-content: center;
    align-content: center;
    border-radius: 100%;

}

.users{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-family: number;
    font-size: 1.5rem;
}

.buttonActive{
    height: 40px;
    width: 40px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(../../assets/default/test.jpg);
    background-size: contain;
    color: #fff;
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 800;
}

.input {
    height: 40px;
    width: 80%;
    text-indent: 10px;
    border-radius: 5px;
    border: 1px solid;
    margin-top: 20px;
    border: 1px solid #eee;
    display: block;
    font-size: 1rem;
    padding: 0.8rem;
    border-radius: 0.4rem;
    background-color: rgb(255, 255, 255);
    transition: 0.1s;
  }

  .input:focus,
  .input:hover {
    outline: none;
    border-color: #56298b;
    background-color: white;
    box-shadow: 0 0 0 3px #ac7fe3ab;
  }

  .button {
    font-size: 1rem;
    font-family: var(--type-first);
    cursor: pointer;
    border: none;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.4rem;
    margin-top: 40px;
    background-color: #8475d4;
    color: #fffffff1;
    padding: 0.8rem 3.2rem;
    box-sizing: border-box;
    transition: 0.2s;
  }

  .button:hover,
  .button:focus {
    outline: none;
    box-shadow: 0 0 0 3px #dcc0ffab, 0 0 0 4px #6f55ff;
  }

  .button:disabled {
    opacity: 0.5;
    cursor: wait;
  }

  .groupContainer.block{
    opacity: 50%;
    
  }