.container {
  min-height: 100vh;
}

.boxContent {
  height: 180px;
  width: 180px;
  border-radius: 5px;
  display: flex;
  position: relative;
  background-color: rgb(255, 255, 255);
  align-items: center;
  justify-content: center;
  transition: 0.2s;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.boxContent:hover {
  box-shadow: rgba(0, 0, 0, 0.096) 1.95px 1.95px 2.6px;
}

.boxContent img {
  width: 70%;
}
h4 {
  text-align: center;
  margin-top: 20px;
  color: #333;
  margin-bottom: 10px;
}

.boxfixed {
  display: flex;
  font-size: 1.2rem;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  opacity: 0;
  transform: translateX(-20px);
  animation: anime-left 0.3s forwards;
}

@keyframes anime-left {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.notification {
  height: 20px;
  background-color: rgb(70, 228, 118);
  width: 20px;
  border-radius: 100%;
  position: absolute;
  color: rgb(255, 255, 255);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.6rem;
  right: -5px;
  top: -5px;
}

.progressbar {
  width: 100%;
  position: absolute;
  height: 3px;
  border-radius: 5px;

  bottom: 0;
}

.progressbarElement {
  width: 100%;
  position: absolute;
  height: 3px;
  border-radius: 5px;
  bottom: 0;
}

.containerActive {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
  justify-content: center;
}
